
















































































































































import Vue from "vue";
import { IComponentCollection } from "../../../../../common/shrimp/interfaces/ComponentInterfaces/IComponentCollection";

export default Vue.extend({
  name: "groupBox",
  props: {
    formName: {
      type: String,
      default: null
    },
    __domElements: {
      type: Object,
      default: null
    },
    toggleContent: {
      type: Boolean,
      default: false
    },
    showToggleArrow: {
      type: Boolean,
      default: false
    },
    disableToggle: {
      type: Boolean,
      default: false
    },
    header: {
      type: String,
      default: ""
    },
    rightHeader: {
      type: String,
      default: ""
    },
    leftSubHeader: {
      type: String,
      default: ""
    },
    leftSubHeaderFontSize: {
      type: String,
      default: "10pt"
    },
    leftSubHeaderFontColor: {
      type: String,
      default: "#555;"
    },
    rightSubHeader: {
      type: String,
      default: ""
    },
    rightSubHeaderFontSize: {
      type: String,
      default: "10pt"
    },
    rightSubHeaderFontColor: {
      type: String,
      default: "#555;"
    },
    backGroundColor: {
      type: String,
      default: "white"
    },
    displayData: {
      type: String,
      default: "block"
    },
    showHeader: {
      type: Boolean,
      default: false
    },
    defaultDiv: {
      type: Boolean,
      default: true
    },
    customStyle: {
      type: String,
      default: ""
    },
    _renderingProps: {
      type: Object,
      default: null
    },
    isTableCell: {
      type: Boolean,
      default: false
    },
    colspan: {
      type: [String, Number],
      default: null
    }
  },
  mounted() {
    if ((<any>this).isRendered === false) {
      return;
    }
    let holderDiv = <HTMLElement>this.$refs["holder"];
    for (let element of this.__domElements.default) {
      holderDiv.appendChild(element);
    }
  },
  data() {
    return {
      toggleContentData: this.toggleContent,
      toggleTitle: this.header,
      toggleRightTitle: this.rightHeader,
      bGColor: this.backGroundColor,
      rigthSubtitle: this.rightSubHeader,
      leftSubtitle: this.leftSubHeader
    };
  },
  created() {
  },
  methods: {
    switchContent() {
      if (this.disableToggle) { return; }
      (<any>this).toggleContentData = !(<any>this).toggleContentData;
      let icon = this.$refs["toggle-arrow-down"];
    },
    getHeaderClass() {
      return "toggle-header";
    },
    resetForm() {
      let componentCollection: IComponentCollection = this._renderingProps.context().contextItems.Dory.GetComponentCollectionByID(this._renderingProps.props().ID);
      (<any>this).arrangeChildElements(componentCollection, "reset");
      (<any>this).$validator.reset();
    },
    disableForm(state: boolean) {
      let componentCollection: IComponentCollection = this._renderingProps.context().contextItems.Dory.GetComponentCollectionByID(this._renderingProps.props().ID);
      (<any>this).arrangeChildElements(componentCollection, "disable", state);
    },
    arrangeChildElements(componentCollection: IComponentCollection, type: string, state: boolean) {
      const self = (<any>this);
      if (componentCollection.children) {
        for (let index = 0; index < componentCollection.children.default.length; index++) {
          const element = componentCollection.children.default[index];
          self.$nextTick(() => {
            if (type == "disable") {
              element.component!['disabled'] = state ? true : false
            }
            if (type == "reset") {
              if (element.component!["dataValue"]) {
                element.component!["dataValue"] = null;
              }
              else if (element.component!["radioGroupValue"]) {
                element.component!["radioGroupValue"] = null;
              }
              else if (element.component!["valueData"]) {
                element.component!["valueData"] = null;
              }
            }
          })
          self.arrangeChildElements(element, type, state);
        }
      }
    }
  },
  computed: {
    headerClass() {
      return (<any>this).getHeaderClass();
    },
    validateAll() {
      if ((<any>this).isRendered === false) {
        return false;
      }
      if ((<any>this).isDisabled === false) {
        return false;
      }
      return true;
    }
  }
})
